import StyledPost from './post'
import { markdownToText } from '../util'
import React from 'react'
import { Link } from 'react-static'
import cx from 'classnames'

const preview_size = 200

export default ({post}) => (
  <StyledPost
    key={post.basename}
    home_template
  >
    <header className="post-header">
      <h2 className="post-title">
        <Link to={`/post/${post.date}/${post.basename}/`}>{post.title}</Link>
      </h2>
    </header>
    <section className="post-excerpt">
      <p>
        {
          markdownToText(post.body)(preview_size)
        }
        <Link className="read-more" to={`/post/${post.date}/${post.basename}/`}>»</Link>
      </p>
    </section>
    <footer className="post-meta">
      {
        post.author &&
        <Link to={`/authors/${post.author}`}>{post.author}</Link>
      }
      <time className={cx('post-date', {'has-author': post.author})} dateTime={post.date}>{post.date}</time>
    </footer>
  </StyledPost>
)



