import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-static'
import { backtop } from '../util'

function handleClick(e) {
  let el = e.target
  while(el.nodeName.toLowerCase() !== 'a' && el !== e.currentTarget) {
    el = el.parentNode
  }

  if (el.nodeName.toLowerCase() === 'a') {
    backtop(e)
  }
}

const Paginnation = ({
  pages,
  page_no,
  page_size
}) => (
  <Root onClick={handleClick}>
    {
      page_no > 1 &&
      <Btn className="prev" to={`?page_no=${page_no - 1}`}>
        <i className="iconfont icon-arrow"></i>
      </Btn>
    }
    <Show>Page {page_no} of {pages}</Show>
    {
      page_no < pages &&
      <Btn className="next" to={`?page_no=${page_no + 1}`}>
        <i className="iconfont icon-arrow"></i>
      </Btn>
    }
  </Root>
)

export default Paginnation

Paginnation.PropsType = {
  // 总页码
  pages: PropTypes.number.required,
  page_no: PropTypes.number,
  page_size: PropTypes.number
}

const Root = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9eabb3;
  font-size: 1.3rem;
  margin-bottom: 3rem;
  user-select: none;
`

const Btn = styled(Link)`
  display: inline-block;

  i {
    display: inline-block;
    font-size: 3rem;
    font-weight: 100;
    color: #00000082;
    transition: color .3s ease;
  }

  &:hover i {
    color: #111;
  }

  &.prev i {
    transform: rotate(-90deg);
  }

  &.next i {
    transform: rotate(90deg);
  }
`

const Show = styled.div`
  margin: 0 2.5rem;
`



