import React from 'react'
import { withRouteData } from 'react-static'
import Pagination from '../components/pagination'
import qs from 'qs'
import styled from 'styled-components'
import {render} from 'react-dom'
import PostHome from '../components/post-home'
import sortBy from 'lodash.sortby'

// 分页文章数
const page_size = 15

export default withRouteData(({ posts, history: {location: {search}} }) => {
  const query = qs.parse(search.slice(1))
  const pages = Math.ceil(posts.length / page_size)
  let {page_no = 1} = query
  page_no = +page_no

  if (page_no < 1) page_no = 1
  if (page_no > pages) page_no = pages

  return (
    <div>
      <div>
        {sortBy(posts, 'date').reverse().slice(page_size * (page_no - 1), page_size * page_no).map(post => (
          <PostHome
            key={post.title}
            post={post}
            page_size={page_size}
          />
        ))}
      </div>
      <Pagination
        pages={pages}
        page_no={page_no}
        page_size={page_size}
      />
    </div>
  )
})



