import React from 'react'
import { Router, Link } from 'react-static'
import { hot } from 'react-hot-loader'
import Routes from 'react-static-routes'
import './app.css'
import styled, {keyframes} from 'styled-components'
import Headroom from 'react-headroom'
import cx from 'classnames'
import Helmet from 'react-helmet'
import ReactTooltip from 'react-tooltip'
import { backtop } from './util'
import Search from './components/search'
import ClickOutside from 'react-click-outside'
import Logo from './logo.svg'

const is_mobile = () => typeof window !== 'undefined' && 'ontouchstart' in window

const description = `这是一个互联网相关的技术类博客、开发笔记，提供全面的、前沿的计算机前端、后端教程，包括最新的国外技术文章，掌握最新的互联网技术资讯、趋势`
const keywords = ['前端', '后端', 'Java', 'Javascript', 'js', '开发', '教程', '技术']
const title = `代码拾遗`
const type = `文章`

const App = class extends React.Component {
  state = {
    isMobile: false,
    showSearch: false
  }

  toggleSearch() {
    this.setState({
      showSearch: !this.state.showSearch
    })
  }

  handleKeyPress = e => {
    if (e.which === 13) {
      console.log(this.props)
    }
  }

  componentDidMount() {
    if (is_mobile()) {
      this.setState({
        isMobile: true,
      })
    } else {
      this.setState({
        isPc: true
      })
    }
  }

  render() {
    return (
      <Router>
        <Root className={cx({
          'is-mobile': this.state.isMobile,
          'is-pc': this.state.isPc
        })}>
        <Helmet>
          <meta property="og:description" content={description} />
          <meta property="og:title" content={title} />
          <meta property="og:type" content={type} />
          <meta name="title" content={title} />
          <meta name="description" content={description} />
          <meta name="keywords" content={keywords.join(',')} />
          <title>{title}</title>
        </Helmet>
          <Headroom>
            <Title style={{backgroundImage: `url(${Logo})`}}></Title>
            <Nav>
              <Link to="/" className="icon-link">
                <i className="iconfont icon-home"></i>
              </Link>
              <Popular>
                <Link to="/tags/frontend">前端</Link>
                <Link to="/tags/backend">后端</Link>
                <Link to="/tags/mobile">移动端</Link>
                <Link to="/tags/java">Java</Link>
                {/*
                <Link to="/tags/javascript">JavaScript</Link>
                */}
              </Popular>
              <ToggledPopular onClick={e => {
                if (!is_mobile()) return
                const el = e.currentTarget
                const list = e.currentTarget.children[1]
                list.classList.toggle('show')
                function handleClickOutside (e) {
                  if (el.contains(e.target)) return

                  list.classList.remove('show')
                  document.removeEventListener('click', handleClickOutside)
                }

                if (list.classList.contains('show')) {
                  document.addEventListener('click', handleClickOutside)
                }
              }} className="toggled-popular">
                <i className="iconfont icon-popular"></i>
                <ul>
                  {/*
                  <li>
                    <Link to="/tags/frontend">前端</Link>
                  </li>
                  */}
                  <li>
                    <Link to="/tags/backend">后端</Link>
                  </li>
                  <li>
                    <Link to="/tags/mobile">移动端</Link>
                  </li>
                  <li>
                    <Link to="/tags/java">Java</Link>
                  </li>
                  {/*
                  <li>
                    <Link to="/tags/javascript">JavaScript</Link>
                  </li>
                  */}
                </ul>
              </ToggledPopular>
              <Right>
                <button className="search-btn icon-btn" onClick={this.toggleSearch.bind(this)}>
                  <i className="iconfont icon-search"></i>
                </button>
                {
                  this.state.showSearch &&
                    <ClickOutside className="search-input" onClickOutside={this.toggleSearch.bind(this)}>
                      <Search onKeyPress={this.handleKeyPress} />
                    </ClickOutside>
                }
                <Link data-tip="归档" className="archive icon-link" to="/archives">
                  <i className="iconfont icon-archive"></i>
                </Link>
              </Right>
            </Nav>
          </Headroom>
          <div className="content">
            <Routes />
          </div>
          {
            !is_mobile() &&
            <ReactTooltip effect="solid" place="left" />
          }
        </Root>
      </Router>
    )
  }
}

export default hot(module)(App)

const Root = styled.div``

const Nav = styled.nav`
  padding: 0 40px;
  display: flex;
  align-items: center;

  @media only screen and (max-width: 400px) {
    padding: 0 20px;
  }

  a {
    font-size: 1.6rem;
    font-weight: normal;
  }

  .icon-link {
    font-size: 1.8rem;
  }

  .icon-btn {
    -webkit-apparance: none;
    background-color: transparent;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
  }
`

const SlideRightIn = keyframes`
  0% {
    width: 0px;
    padding: 4px 0;
  }

  100% {
    width: 160px;
    padding: 4px 5px;
  }
`

const Right = styled.div`
  margin-left: auto;
  display: flex;
  align-items: center;
  overflow: hidden;

  .search-input input {
    -webkit-apparance: none;
    border: 0;
    outline: 0;
    padding: 4px 5px;
    margin: 0;
    background-color: #fff;
    width: 160px;
    animation: .2s ${SlideRightIn} ease;
  }
`

const Popular = styled.span`
  @media only screen and (max-width: 920px) {
    display: none;
  }

  a {
    font-size: 1.4rem;
  }

  &::before {
    content: '';
    height: 2.1rem;
    width: 1px;
    background-color: #00000038;
    display: inline-block;
    vertical-align: middle;
  }
`

const ToggledPopular = styled.span`
  display: none;
  padding: 0;
  margin: 0;
  position: relative;
  font-size: 1.4rem;

  &::before {
    content: '';
    height: 2.1rem;
    width: 1px;
    background-color: #00000038;
    display: inline-block;
    vertical-align: middle;
  }

  > i {
    display: inline-block;
    padding: 1rem;
    color: #fff;
    font-weight: normal;
    transition: color .3s ease;
  }

  a {
    font-size: 1.4rem;
    font-weight: normal;
    line-height: 1;
    display: block;
    padding: 1.2rem 3rem 1.2rem 1rem;
  }

  li {
    margin: 0;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #108db8;
    display: none;
    width: 120px;
    padding: 10px 0;
  }

  ul.show {
    display: inline-block;
  }

  @media only screen and (max-width: 920px) {
    display: inline;
  }
`

const Title = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  top: 50%;
  color: #fff;
  transform: translate(-50%,-50%);
  background-repeat: no-repeat;
  background-size: 100% auto;
  width: 8rem;
  height: 2rem
`



