import React from 'react'
import { withRouteData, Link } from 'react-static'
import Markdown from 'react-markdown'
import universal from 'react-universal-component'
import styled from 'styled-components'
import moment from 'moment'
import StyledPost from '../components/post'
import '../github-markdown.css'
import '../qtcreator_dark.css'
import hlj from 'highlight.js'
import Helmet from 'react-helmet'
import { markdownToText } from '../util'
import AfterRender from '../components/after-render'
import CodeBlock from '../components/code-block'
import LinkBlock from '../components/link-block'
import ImageBlock from '../components/image-block'
import ParagraphBlock from '../components/paragraph-block'
import cx from 'classnames'

moment.locale('zh-cn')

export default withRouteData(({post}) => (
  <StyledPost>
    <Helmet>
      <meta property="og:description" content={markdownToText(post.body)(160).trim()} />
      <meta property="og:title" content={post.title} />
      <meta property="og:type" content="article" />
      <meta name="description" content={markdownToText(post.body)(160).trim()} />
      <meta name="keywords" content={post.tags.join(',')} />
    </Helmet>
    <header className="post-header">
      <h1 className="post-title">{post.title}</h1>
      <section className={`post-meta `}>
        {
          post.author &&
          <Link to={`/authors/${post.author}`}>{post.author}</Link>
        }
        <time
          className={cx('post-date', {
            'has-author': post.author
          })}
          dateTime={post.date}>{moment(post.date).format('l')}</time>
        <span>, </span>
        <span>{post.tags.map((tag, i) => (
          <span key={tag}>
            <Link to={`/tags/${tag.toLowerCase()}`}>{tag}</Link>
            {
              i !== post.tags.length - 1 &&
              <span>, </span>
            }
          </span>
        ))}</span>
      </section>
    </header>
    <section className="post-content markdown-body">
      <Markdown
        source={post.body}
        transformImageUri={uri => uri.indexOf('http') !== 0 ? `http://p6jqy6mfr.bkt.clouddn.com/${uri}` : uri}
        renderers={{code: CodeBlock, link: LinkBlock, image: ImageBlock, paragraph: ParagraphBlock}}
      />
    </section>
    <section className="post-footer"></section>
  </StyledPost>
))



